import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { MONTHYEARS } from '../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { CONSOLIDAR_NUMBER_ENUM_VALUES, GRUPO_ATUAL_NUMBER, NORMAL_NUMBER } from '../../../../../models/enumerated/mcg/consolidarEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportFormView from '../../../report/ReportFormView';
import { BalanceteCaixaRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './balanceteCaixaRV';

export default function BalanceteCaixaReportView(props: WindowComponentProps) {
  const history = useHistory();
  const formProps = useForm<BalanceteCaixaRV>({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const [isEnabledIsMatriz, setIsEnabledIsMatriz] = useState(false);

  const basicdata: PrinterDataBasic = {
    ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, MONTHYEARS.value),
    module: 'MCG',
    refFinal: globalReducer.globalParameter.empresaMCG.refFinal,
    refInicial: globalReducer.globalParameter.empresaMCG.refAtual,
  };

  const carregarParametroEmpresa = () => {
    if (globalReducer.globalParameter.empresa.matriz === null) {
      setIsEnabledIsMatriz(true);
      formProps.getValues().isAcumularMatrizFilial = globalReducer.globalParameter.empresa.centralizar;
    }
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        setTimeout(() => {
          const {
            empresa: { id, grupoEmpresa, grupoEmpresaRaiz },
          } = globalReducer.globalParameter;
          carregarParametroEmpresa();
          formProps.register({ name: 'idGerEmpresa' });
          formProps.register({ name: 'idGerGrupoEmpresaAtual' });
          formProps.register({ name: 'idGerGrupoEmpresaRaiz' });
          formProps.register({ name: 'idsGerGrupoEmpresaPais' });
          formProps.setValue('consolidar', NORMAL_NUMBER.value, true);
          formProps.setValue('idGerEmpresa', id, true);
          formProps.setValue('idGerGrupoEmpresaAtual', grupoEmpresa.id, true);
          formProps.setValue('idGerGrupoEmpresaRaiz', grupoEmpresaRaiz.id, true);
          formProps.setValue('idsGerGrupoEmpresaPais', globalReducer.globalParameter.idsGrupoEmpresaPais, true);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const adjustTitle = () => {
    let consolidar: string = '';

    if (formProps.getValues().consolidar !== NORMAL_NUMBER.value) {
      const titleconsolidar = formProps.getValues().consolidar === GRUPO_ATUAL_NUMBER.value ? 'Grupo Atual' : 'Hierarquia Inferior';
      consolidar = ' (' + titleconsolidar + ')';
    }

    let reportTitle: string = REPORT_TITLE;

    if (formProps.getValues().isAcumularMatrizFilial) {
      reportTitle += '- Consolidade';
    }

    formProps.setValue('title', reportTitle + consolidar);
  };

  const acumularMatrizFilial = isEnabledIsMatriz ? (
    <Col>
      <CustomInput
        type="switch"
        onChange={adjustTitle}
        label="Acumular Matriz e Filiais"
        name="isAcumularMatrizFilial"
        id="isAcumularMatrizFilial"
        innerRef={formProps.register}
      />
    </Col>
  ) : null;

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      wikiId={3472}
    >
      <ReportFormView formProps={formProps} printerData={basicdata} history={history}>
        <Row className="mb-3">
          <Col>
            <CustomInput type="switch" label="Somente Conta Caixa" name="somenteCaixa" id="somenteCaixa" innerRef={formProps.register} />
          </Col>
          <Col>
            <CustomInput type="switch" label="Imprimir Data/Hora de Emissão" name="dataHora" id="dataHora" innerRef={formProps.register} />
          </Col>
          <Col>
            <CustomInput
              type="switch"
              label="Totalizar Por Histórico Padrão"
              name="totalizarHistorico"
              id="totalizarHistorico"
              innerRef={formProps.register}
            />
          </Col>
          {acumularMatrizFilial}
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="consolidar"
              label="Consolidação"
              isPrependLabel={true}
              onChange={adjustTitle}
              placeholder="Selecione o tipo de relatório"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: CONSOLIDAR_NUMBER_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </ReportFormView>
    </WindowComponent>
  );
}
